$search-box-outline: rgb(105, 201, 214);

.post-container {
  padding: 2em 0.3em;
  margin-top: 1em;
}

.search-box {
  height: 2em;
  border: none;
  border-radius: 5px;
  border-width: 0;
  padding-left: 0.5em;
  width: 100%;
  margin-bottom: 1em;
  &:focus {
    outline-color: $search-box-outline;
  }
}
